.form-intro {
  padding: 36px 20px 28px;
  position: relative;
}

.form-intro::after {
  content: "";
  position: absolute;
  width: 320px;
  height: 1px;
  background-color: #e5e5e5;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: @tablet-width) {
  .form-intro {
    padding: 64px 166px 102px 167px;
  }

  .form-intro::after {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .form-intro {
    padding: 115px 382px 67px 382px;
  }
}

.form-intro__title {
  margin: 0;
  padding: 0;
  .text-size (18px, 26px);
  text-align: center;
  margin-bottom: 24px;
}

@media (min-width: @tablet-width) {
  .form-intro__title {
    .text-size (30px, 26px);
    margin-bottom: 20px;
  }
}

.form-intro__message {
  margin: 0;
  padding: 0;
  .text-size (14px, 21px);
  text-align: center;
}
